<template>
<div class="telephone">
  <div class="buyheader">
    <h1>产品合作</h1>
  </div>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>产品合作</el-breadcrumb-item>
  </el-breadcrumb>
  <div class="plus">
    <i class="el-icon-circle-plus" @click="open3"></i>
  </div>

</div>
</template>

<script>
export default {
  data() {
    return {
      fileList2: [{
        name: 'food.jpeg',
        url: 'http://ooi407n8x.bkt.clouddn.com/an.jpg',
      }],
      dialogVisible: false
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    open3() {
      this.$prompt('请输入id：', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(({
        value
      }) => {
        this.$message({
          type: 'success',
          message: '提交成功' + value
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        });
      });
    }
  }
}
</script>

<style lang="scss" scoped>

.el-breadcrumb {
    padding-left: 15px;
    width: 100%;
}

.number{
  margin:5px 15px;
  padding: 10px;
  background: #461b93;
  color:#fff;
  border-radius: 2px;
}

li {
    background-color: #f5f7fa;
    margin: 15px;
    padding: 15px;
    border-radius: 4px;
    .e-on:nth-child(1) {
        width: 100%;
    }
    .e-on:nth-child(2) {
        width: 100%;
    }
    .e-on {
        padding: 10px 0;
        background-color: #f5f7fa;
        span {
            color: #99a9bf;
        }
    }
}

.plus {
    text-align: center;
    font-size: 40px;
    margin-bottom: 30px;
    color: #461b93;
}
</style>



